import React, { Component } from 'react';

import { Card, Row, Table, Col, message } from 'antd';
import { getListStudyField, deleteStudyField } from 'services/StudyField';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { ColEditDelete } from 'components/common';

class SpecializedTraining extends Component {
  constructor(props) {
    super(props);
    this.state = {
      specialized: [],
      loading: false,
      pagination: {},
    };
  }
  componentDidMount() {
    this.getListData();
  }

  getListData = page => {
    this.setState({
      loading: true,
    });
    getListStudyField(page, true, true)
      .then(res => {
        const pagination = { ...this.state.pagination };
        pagination.total = res.data.totalResults;
        this.setState({
          specialized: res.data.items,
          loading: false,
          pagination,
        });
      })
      .catch(error => {
        console.log('====>', error);
        this.setState({
          loading: false,
        });
      });
  };

  handleTableChange = pagination => {
    const pager = { ...this.state.pagination };
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.getListData(pagination.current);
  };

  confirmDelete = e => {
    this.setState({
      loading: true,
    });
    deleteStudyField(e)
      .then(() => {
        message.success('Xa ngnh o to thnh cng');
        this.getListData();
        this.setState({
          loading: false,
        });
      })
      .catch(error => {
        this.setState({
          loading: false,
        });
        console.log('res', error);
        // message.error(error.response.data.reason);
      });
  };

  render() {
    const { pagination, specialized, loading } = this.state;
    this.columns = [
      {
        title: 'Ngnh o to',
        dataIndex: 'parentLabel',
        key: 'parentLabel',
      },
      {
        title: 'M chuyn ngnh',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tn chuyn ngnh',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tn ting anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#specializeTraining/form/edit/${record.id}`}
            onConfirmDelete={() => this.confirmDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Danh sch chuyn ngnh">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/specializeTraining/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              bordered
              dataSource={specialized}
              columns={this.columns}
              pagination={pagination}
              onChange={this.handleTableChange}
              loading={loading}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <SpecializedTraining />
  </Page>
);



